import clsx from 'clsx';
import React from 'react';

import { useMode } from '../../providers/mode';

export default function Title() {
    const { isDark } = useMode();

    return (
        <div className={clsx('title-wrapper', isDark && 'title-wrapper-dark')}>
            <div className="title-box">
                <h1 className={clsx('title', isDark && 'title-dark')}>Explore DeFi ecosystem</h1>
                <p className={clsx('description', isDark && 'description-dark')}>Your<span role="img" aria-label="information"> 😎</span> DeFi <span role="img" aria-label="information"> 📊</span>Dashboard (attention the site is under<span role="img" aria-label="information"> 👨‍🏭️</span> construction<span role="img" aria-label="information"> 🛠️</span>)</p>
            </div>
        </div>
    );
}
